@import url(https://js.arcgis.com/4.19/@arcgis/core/assets/esri/themes/light/main.css);
html,
body,
#root {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header {
  height: 70px;
  background-color: rgb(165, 233, 168);
}

.header-title {
  color: white !important;
  margin: 10px;
}

.left-col {
  padding: 30px 20px;
  height: 100vh;
}
.left-col-title {
  color: white !important;
  border-radius: 5px;
  padding: 5px 10px;
  background-color: rgb(165, 233, 168);
}

.rightcol {
  height: 800px;
}
.esri-popup {
  visibility: hidden;
}
.esri-spinner {
  visibility: hidden;
}

#mapDiv {
  padding: 0;
  margin: 0;
  height: calc(100vh - 75px);
}



